<template>
	<div id="view-main">
		<div class="fixed-part">
			<div class="header">
				<div class="tabbed-navs">
					<div class="tabbed-nav">
						<p>Current Walkins</p>
						<span>
						<i class="fa fa-arrow-left" v-on:click="collapseSearch"></i>
						<input  @keyup="inputKeyUp" @input="debounceSearch" type="text" placeholder="Search">
						<i class="fa fa-search" v-on:click="expandSearch"></i>
						<i class="fa fa-times app-hidden" v-on:click="clearSearch"></i>
						</span>
					</div>
				</div>
			</div>
		</div>
		<transition appear name="fade">
			<router-view></router-view>
		</transition>	
	</div>
</template>

<script>
	import _ from 'lodash'
	export default {
	    data : function() {
	    	return {
	    		isActive : true,
	    		crossIsVisible : false,
	    		counter : 0,
	    		currentSearchTerm : ''
	    	}
	    },
	    methods: {
	        debounceSearch : _.debounce(function(event){        	
	        	this.$root.$data.isDataOver = false;
	        	if(event.target.value.length!=0){
	        	console.log("The length of search : " + event.target.value.length);
	        	this.$root.$emit('get-query-data',event.target.value);
	        	}
	        	else{
	        	console.log("The length of search bar : " + event.target.value.length);	
	        	this.$root.$emit('get-query-data','');	
	        	}
	        },500),
	        expandSearch : function(){
	        	document.querySelector('.tabbed-nav span').style.width = 100+'vw';
	        	document.querySelector(".tabbed-nav input").focus();
	        	setTimeout(function(){
	        	document.querySelector('.tabbed-nav .fa-arrow-left').style.opacity = 1;
	        	}, 300);
	        },
	        collapseSearch : function() {
	        	if(document.querySelector('.tabbed-nav input').value.length>0){
	        		document.querySelector('.tabbed-nav .fa-times').classList.toggle('app-hidden');
	        		document.querySelector('.tabbed-nav .fa-search').classList.toggle('app-hidden');
	        	}
	        	document.querySelector('.tabbed-nav span').style.width = 0;
	        	document.querySelector('.tabbed-nav .fa-arrow-left').style.opacity = 0;
	        	document.querySelector('.tabbed-nav input').value = '';
	        	var self = this;
	        	setTimeout(function(){
	        	self.$root.$emit('get-query-data','');
	        	}, 500);
	        },
	        inputKeyUp : function(){
				if(document.querySelector('.tabbed-nav input').value.length>0 && this.counter==0){
					document.querySelector('.tabbed-nav .fa-times').classList.toggle('app-hidden');
	        		document.querySelector('.tabbed-nav .fa-search').classList.toggle('app-hidden');
	        		this.counter = 1;
				}
				else if(document.querySelector('.tabbed-nav input').value.length==0){
					document.querySelector('.tabbed-nav .fa-times').classList.toggle('app-hidden');
	        		document.querySelector('.tabbed-nav .fa-search').classList.toggle('app-hidden');
	        		this.counter = 0;
				} 
	        },
	        clearSearch : function(){
	        	document.querySelector('.tabbed-nav input').value = '';
	        	document.querySelector('.tabbed-nav .fa-times').classList.toggle('app-hidden');
	        	document.querySelector('.tabbed-nav .fa-search').classList.toggle('app-hidden');
	        	this.counter=0;
	        	this.$root.$emit('get-query-data','');
	        }
	    },
	    created : function(){
	    		console.log('created');
	    		console.log(this.$root.$data.redirectFromDetail)
	    },
	    updated : function(){
	    		console.log('updated');
	    },
	    mounted : function() {
	    	    console.log('mounted');
	    		if(this.$root.$data.currentSearchTerm.length>0){
		    		if(this.$root.$data.redirectFromDetail==true)
		    		{
		    			console.log('inside loop');
		    			this.expandSearch();
		    			console.log("current search term is"+this.$root.$data.currentSearchTerm);
		    			document.querySelector('.tabbed-nav input').value = this.$root.$data.currentSearchTerm;
		    			this.$root.$data.redirectFromDetail = false;	
		    		}
	    		}
	    }, 
	    destroyed : function(){
	    	console.log("setting current search term");
	    	this.$root.$data.currentSearchTerm = document.querySelector('.tabbed-nav input').value;
	    }
	}
</script>

<style>

	body{
		box-sizing: border-box;
	}

	#view-main{
		/*height: 100vh;*/
		box-sizing: border-box;
		padding : 25px 0;
 	}

	.header{
		width: inherit;
		position: relative;
	}

	
	.brZero{
		border-radius: 0px;
		border : 3px solid #009688;
	}

	.fontZero {
		font-size: 0px !important;
	}

	.fontFifty {
		font-size: 50px !important;
	}

	.fa-times {
		font-size: 0px;
	}

	.tabbed-nav{
		width: 100%;
		float: left;
		text-align: left;
	}

	.tabbed-nav span{
		position: absolute;
    	right: 0px;
    	top: 0px;
    	width: 0px;
    	transition: all 0.2s ease;
    	box-sizing: border-box;
	}

	.tabbed-nav span input{
		height: 158px;
	    width: 100%;
	    border: none;
	    padding-left: 175px;
	    padding-right: 150px;
	    box-sizing: border-box;
	}

	.tabbed-nav span input,
		input::-webkit-input-placeholder {
	    font-size: 45px;
	    vertical-align: baseline;
	    padding-top: 15px;
    }


	.tabbed-nav span .fa-search {
		position: absolute;
	    right: 40px;
	    top: 50px;
	    font-size: 50px;
	    color: white;
	}

	.tabbed-nav span .fa-times{
		position: absolute;
	    right: 40px;
	    top: 57.5px;
	    font-size: 50px;
	    color: darkslategrey;	
	}

	.app-hidden{
		display: none;
	}


	.tabbed-nav span .fa-arrow-left{
		position: absolute;
		top : 5px;
		left:  0px;
		font-size: 60px;
		color: grey;
		padding: 45px;
		opacity: 0;
		transition: opacity 0.3s;
	}

	.tabbed-nav p {
		font-size: 50px;
		color: white;
		padding-bottom: 50px;
	    font-weight: bold;
	    letter-spacing: 3px;
	    margin-top: 0px;
	    padding-top: 50px;
		transition: border-bottom 1s ;
		margin-bottom: 0px;
		padding-left: 40px;
        box-shadow: 0 5px 5px 1px rgba(0,0,0,0.5);
	}

	.fixed-part{
		position: fixed;
		top: 0;
		width: 100%;
		background-color: #1976D2;
		z-index: 3;
	}

	.fade-enter-active, {
	  transition: opacity .25s
	}
	.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
	  opacity: 0
	}

	
	.circle {
	    position: relative;
	    display: block;
	    height: 24px;
	    width: 24px;
	    background-color: white;
	    border-radius: 70px;
	    z-index: 0;
	    border: 10px solid #009688;
	    -webkit-transition: all 0.6s cubic-bezier(0.25, 1.7, 0.35, 0.8);
	    transition: all 0.6s cubic-bezier(0.25, 1.7, 0.35, 0.8);
	    z-index: 1;
	}

	.handle {
		 position: absolute;
	    background-color: #009688;
	    top: 45px;
	    left: 10px;
	    width: 55px;
	    height: 10px;
	    display: block;
	    transform: rotate(45deg);
	    float: right;
	    border-radius: 2px; 
	}
	.handle:after {
		content: "";
	    background-color: rgb(0, 150, 136);
	    width: 55px;
	    height: 10px;
	    display: block;
	    float: right;
	    z-index: -1;
	    border-radius: 2px;
	    transition: all 0.6s cubic-bezier(0.25, 1.7, 0.35, 0.8);
	}

	.open .circle {
	  -webkit-transform: scale(0.02);
	          transform: scale(0.02);
	  -webkit-transform-origin: 30% 40%;
	          transform-origin: 30% 40%;
	  background: white;
	  border-color: white;
	}
	.open .handle:after {
	  -webkit-transform-origin: center center;
	          transform-origin: center center;
	  -webkit-transform: rotate(90deg);
	          transform: rotate(90deg);
	}

	
	
</style>


